<template>
    <div class="role_manage">
      <div class="role-manage-main">
        <!-- <separator></separator> -->
        <div class="buttonber">
          <i class="blue"></i>
          <span class="title">{{$t("roleManage.title")}}</span>
            <el-button @click="createUser" :disabled="!$route.meta['add']">{{$t("roleManage.add")}}</el-button>
        </div>
            <div class="table-box" ref="tableBox">
                <el-table
                    v-loading="tableBoxLoading"
                    ref="table"
                    :data="tableData"
                    :default-sort = "{prop: 'id', order: 'ascending'}"
                    :header-cell-style="{background:'rgba(245,248,252,1)'}"
                    >
                    <el-table-column
                    type="index"
                    :label="$t('roleManage.index')"
                    width="70">
                    </el-table-column>
                    <el-table-column
                    v-for="(tab,index) in tableDataLabel"
                    sortable
                    :key="index"
                    :prop="tab.property"
                    :label="$i18n.locale=='en'?tab.enName:tab.name"
                    :min-width="tab.width">
                    </el-table-column>
                    <el-table-column
                    :label="$t('common.operation')"
                    width="180">
                    <template slot-scope="scope" v-if="scope.row.id!=1">
                        <el-button
                            @click="details(scope.row)"
                            type="text"
                            size="small">
                            {{$t('common.details')}}
                            </el-button>
                            <el-button
                            @click="editPeople(scope.row)"
                            type="text"
                            class="del-btn"
                            size="small"
                            :disabled="!$route.meta['modify']">
                              {{$t('common.edit')}}
                            </el-button>
                            <el-button
                            @click="deleteRow(scope.row)"
                            type="text"
                            class="del-btn"
                            size="small"
                            :disabled="!$route.meta['remove']">
                              {{$t('common.delete')}}
                            </el-button>
                    </template>
                    </el-table-column>
                </el-table>
            </div>
    <el-dialog
      :title="dialogTitle"
      :visible.sync="dialogVisible"
      width="650px"
      @open="openDialog"
      :before-close="handleClose"
      >
      <el-form ref="form" :model="form" label-position='left' label-width="150px" size="mini"style="height:550px;overflow-x:hidden;">
        <el-form-item :label="$t('roleManage.name')"  prop="name" :rules="{ required: true, message:$t('placeholder.plsPersonName')}">
          <el-input  v-model="form.name" :disabled="dialogTitle==$t('common.details')"></el-input>
        </el-form-item>
        <el-form-item :label="$t('roleManage.describe')" prop="remark">
          <el-input
            v-model="form.remark"
            :disabled="dialogTitle==$t('common.details')"
            clearable>
          </el-input>
        </el-form-item>
        <el-form-item :label="$t('roleManage.selection')" prop="remark">
            <div class="tree_org" v-show="false">
              <el-tree
                ref="org"
                default-expand-all
                :data="form.sysRoleOrgDtos"
                show-checkbox
                :default-checked-keys="orgDefaultchecked"
                node-key="roleOrgId"
                @check-change="roleOrgCheckChange"
                :props="defaultProps">
              </el-tree>
            </div>
            <div class="tree_menu">
              <el-tree
                ref="menu"
                default-expand-all
                :data="form.sysRoleMenuDtos"
                :default-checked-keys="menusDefaultchecked"
                show-checkbox
                @check-change="roleMenuCheckChange"
                node-key="roleMenuId"
                :props="defaultProps">
              </el-tree>
            </div>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">{{$t("common.cancel")}}</el-button>
        <el-button type="primary" @click="submitForm">{{$t("common.ok")}}</el-button>
      </span>
    </el-dialog>
    </div>
    </div>
</template>
<script>
import separator from "../../../../public/separator.vue";
import TABLE_TYEPS from "../../../../../config/tableType.js";
import APICONFIG from "../../../../../config/api.js";
export default {
  name: "role_manage",
  components: {
    separator
  },
  data () {
    return {
      dialogTitle: "", //弹框名字
      tableDataLabel: [], //表格配置文件
      netAPI: "",
      tableData: null,
      tableBoxLoading: true,
      showOBJ: "", //显示的权限
      dialogVisible: false,
      form: {
        name: "",
        remark: "",
        sysRoleOrgDtos: null,
        sysRoleMenuDtos: null
      },
      // rules: {
      //   name: [{ required: true, message: this.$t('placeholder.plsPersonName'), trigger: "blur" }]
      // },
      defaultProps: {
        children: "children",
        label: "name"
      },
      orgDefaultchecked: [], //默认选中组织
      menusDefaultchecked: [], //默认选中按钮
      orgDefaultcheckedList: [], //树转列表的临时
      menusDefaultcheckedList: [] //树转列表的临时
    };
  },
  computed: {
    // rules() {
    //   return{
    //     name: [{ required: true, message: this.$t('placeholder.plsPersonName'), trigger: "blur" }]
    //   }
    // }
  },
  created () {
    this.tableDataLabel = TABLE_TYEPS.get("role_manage");
    this.netAPI = APICONFIG().get("role_manage");
    this.getData();
  },
  methods: {
    getData () {
      this.$http.get(this.netAPI.find).then(
        res => {
          if (res.body.status == 200) {
            this.tableBoxLoading = false;
            this.tableData = res.body.data;
          }
        }
      );
    },
    createUser (row) {
      //创建时候 需要同时获取两个接口的数据所以使用了 all promise
      let menus = new Promise((resolve, reject) => {
        this.getUserData(this.netAPI.menus, '', (res, scope) => {
          resolve(res);
        });
      });
      let orgs = new Promise((resolve, reject) => {
        this.getUserData(this.netAPI.org, '', (res, scope) => {
          resolve(res);
        });
      });
      Promise.all([menus, orgs]).then(values => {
        this.dialogTitle = this.$t("roleManage.add");
        this.dialogVisible = true;
        console.log(values[1])
        //找到根生成树
        // this.form.sysRoleOrgDtos = this.findeTreeMain(values[1], "1001", false, true);
        this.form.sysRoleOrgDtos = [];
        this.form.sysRoleMenuDtos = this.findeTreeMain(values[0], "1003");
        console.log(this.form.sysRoleOrgDtos)
      });
    },
    details (row) {
      this.orgDefaultchecked = [];//默认选中组织
      this.menusDefaultchecked = []; //默认选中按钮
      this.getUserData(this.netAPI.details + row.id, row.id, (res, scope) => {
        this.dialogTitle = this.$t("common.details");
        scope.showOBJ = res;
        this.dialogVisible = true;
        this.form.name = res.name;
        this.form.remark = res.remark;
        // this.form.sysRoleOrgDtos = this.findeTreeMain(
        //   res.sysOrgs,
        //   "1001",
        //   true,
        //   true,
        // );
        this.form.sysRoleOrgDtos = []
        console.log(res.sysMenus)
        this.form.sysRoleMenuDtos = this.findeTreeMain(
          res.sysMenus,
          "1003",
          true
        );
      });
    },
    editPeople (row) {
      this.orgDefaultchecked = [];//默认选中组织
      this.menusDefaultchecked = []; //默认选中按钮
      this.getUserData(this.netAPI.details + row.id, row.id, (res, scope) => {
        this.dialogTitle = this.$t("common.edit");
        scope.showOBJ = res;
        this.dialogVisible = true;
        this.form.name = res.name;
        this.form.remark = res.remark;
        // this.form.sysRoleOrgDtos = this.findeTreeMain(res.sysOrgs, "1001", false, false);
        this.form.sysRoleOrgDtos = []
        console.log(res.sysMenus)
        this.form.sysRoleMenuDtos = this.findeTreeMain(res.sysMenus, "1003", false);
      });
    },
    getUserData (url, id = '', callback) {
      //当前的get公用方法
      this.$http.get(url, { params: id }).then(
        res => {
          if (res.body.status == 200) {
            callback(res.body.data, this);
          }
        }
      );
    },
    deleteRow (row) {
      this.$confirm(this.$t("delete.deleteIt"), this.$t("delete.tips"), {
        confirmButtonText: this.$t("delete.ok"),
        cancelButtonText: this.$t("delete.cancel"),
        type: "warning"
      })
        .then(() => {
          this.$http.post(this.netAPI.delete + row.id, { id: row.id }).then(res => {
            if (res.body.status == 200) {
              this.$message({
                type: "success",
                message: this.$t("delete.success")
              });
              this.getData();
            }
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: this.$t("delete.canceledDeletion")
          });
        });
    },
    handleClose () {
      this.dialogVisible = false;
      this.showOBJ = null;
      this.form = {
        roleName: "",
        roleDetails: "",
        orgs: null,
        menus: null,
        name: ""
      };
      this.$refs["form"].resetFields();
      this.orgDefaultcheckedList = [];
      this.menusDefaultcheckedList = [];
    },
    submitForm () {
      let orgs = this.$refs["org"].getCheckedNodes(false, true);
      let menu = this.$refs["menu"].getCheckedNodes(false, true);
      // debugger;
      for (let or of orgs) {
        this.orgDefaultcheckedList.push({ orgId: or.id });
      }
      for (let me of menu) {
        this.menusDefaultcheckedList.push({ menuId: me.id });
      }
      // debugger;
      this.$refs["form"].validate(valid => {
        if (valid) {
          switch (this.dialogTitle) {
            case this.$t("roleManage.add"):
              this.create();
              break;
            case this.$t("common.edit"):
              this.update();
              break;
            default:
              break;
          }
        } else {
          console.log("error submit!!");
          return false;
        }
      });
    },
    create () {
      console.log(this.menusDefaultcheckedList)
      this.$http
        .post(this.netAPI.save, {
          name: this.form.name,
          remark: this.form.remark,
          sysRoleOrgDtos: this.orgDefaultcheckedList,
          sysRoleMenuDtos: this.menusDefaultcheckedList
        }).then(
          res => {
            if (res.body.status == 200) {
              this.handleClose();
              this.getData();
            } else {
              this.$message.success("创建成功!");
              this.handleClose();
            }
          },
          err => {
            this.handleClose();
          }
        );
    },
    update () {
      this.$http
        .put(this.netAPI.put + this.showOBJ.id, {
          id: this.showOBJ.id,
          name: this.form.name,
          remark: this.form.remark,
          sysRoleOrgDtos: this.orgDefaultcheckedList,
          sysRoleMenuDtos: this.menusDefaultcheckedList
        })
        .then(
          res => {
            if (res.body.status == 200) {
              this.handleClose();
              this.getData();
              // this.getRole(res.body.data.id);
              this.$message.success("保存成功!");
            } else {
              this.handleClose();
            }
          },
          err => {
            this.handleClose();
          }
        );
    },
    // getRole(id) {
    //   this.$http.get(this.netAPI.details + id, { "id": id }).then(
    //     res => {
    //       if (res.body.status == 200) {
    //         console.log(res)
    //         res.body.data.sysMenus.map(item => {
    //           if(item.roleMenuId) {
    //             this.roleArr.push(item)
    //           }
    //         })
    //       }
    //     }
    //   ).then(res => {
    //     localStorage.setItem('roleArr', JSON.stringify(this.roleArr));
    //   });
    // },
    toTree (data, parentCode) {
      //生成树
      let tree = [];
      let temp;

      for (let i = 0; i < data.length; i++) {
        if (data[i].parentCode == parentCode) {
          let obj = data[i];
          temp = this.toTree(data, data[i].code);
          if (temp.length > 0) {
            obj.children = temp;
          }
          tree.push(obj);
        }
      }
      return tree;
    },
    toArray (tree, list) {
      //生成队列
      for (let i in tree) {
        if (tree[i].children) {
          list.push(tree[i]);
          this.toArray(tree[i].children, list);
        } else {
          list.push(tree[i]);
        }
      }
    },
    openDialog () {
      this.$nextTick(() => {
        // let keys = this.$refs['menu'].getCheckedKeys();
        // keys.filter(d => d);
        // console.log('open', keys)
        this.$refs['menu'].setCheckedNodes(this.menusDefaultchecked);
      })
    },

    findeTreeMain (obj, str, disabled = false, root = false) {
      for (let item of obj) {
        //生成默认选中(根据类型判断是否选中没有就加上属性因为接口返回的没有属性))
        if (str == "1001") {
          if (item.roleOrgId) {
            this.orgDefaultchecked.push(item.roleOrgId);
          } else {
            item.roleOrgId = null;
          }
        }

        if (str == "1003") {
          if (item.roleMenuId) {
            console.log(item.name + 'xxx' + item.roleMenuId)
            this.menusDefaultchecked.push(item);
          } else {
            console.log(item.name + item.roleMenuId)
            item.roleMenuId = null;
          }
        }
        if (disabled) {
          //disabled 为真就是禁止点击
          item.disabled = true;
        }
      }
      for (let index in obj) {
        //寻找根
        if (obj[index].code == str) {
          if (root) {
            obj[index].parentCode = '1'
            obj.push({ code: '1' })
            return this.toTree(obj, '1');
          } else {
            return this.toTree(obj, str);
          }
        }
      }
    },
    roleOrgCheckChange (node, current, a) {
      // node.roleOrgId = current ? node.id : null;
      // debugger
    },
    roleMenuCheckChange (node, current, a) {
      // node.roleMenuId = current ? node.id : null;
    }
  }
};
</script>
<style lang="scss" scoped>
.role_manage {
  width: 100%;
  height: 100%;
  position: relative;
  .role-manage-main{
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 30px;
    // margin: auto;
    background:rgba(255,255,255,1);
    border:1px solid rgba(233,233,233,1);
    box-shadow:#D1D1D1 0 2px 4px 0;
    border-radius:5px;
    box-sizing: border-box;
    padding: 20px;
    .buttonber {
      text-align: right;
      padding-right: 50px;
      position: relative;
      .blue{
        width:12px;
        height:20px;
        background:rgba(67,131,220,1);
        border-radius:0px 3px 3px 0px;
        position: absolute;
        left: -20px;
        top: 10px;
      }
      .title{
        position: absolute;
        left: 5px;
        top: 9px;
        font-size: 20px;
        font-weight: 600;
      }
    }
    .table-box {
      overflow-y: scroll;
      height: 80%;
      padding: 5px;
    }
    .tree_org {
      width: 48%;
      display: inline-block;
      margin-right: 5px;
      vertical-align: top;
    }
    .tree_menu {
      /*width: 48%;*/
      display: inline-block;
      vertical-align: top;
    }
  }
}
</style>



